/**
 * Code Highlighting for the DOCS website
 *
 * Dependencies:
 *     - highlight: Find the current selected TOC by comparing the URLs
 */

define(["highlightjs"], function (hljs) {
    "use strict";

    // Code blocks
    function highlightCode() {
        $('pre code').each(function (i, block) {
            hljs.highlightBlock(block);
        });
    }

    function runHighlightWorker() {
        var blocks = [];
        var rawTexts = [];
        $('pre code').each(function (i, block) {
            blocks.push(block);
            rawTexts.push(block.textContent);
            rawTexts.push(block.className || "");
        });
        var HighlightWorker = require("worker!./worker.highlight.js");
        var worker = new HighlightWorker();
        worker.onmessage = function (event) {
            var len = event.data.length;
            if (len !== blocks.length) {
                require(["trace"], function (trace) {
                    trace.traceWarning(trace.TraceCategory.performance, "Mismatched lengths for coding highlighted contents.");
                });
                highlightCode();
            }
            else {
                for (var i = 0; i < len; i++) {
                    blocks[i].innerHTML = event.data[i];
                }
            }
        };
        worker.postMessage(rawTexts);
    }

    function run() {
        // Uses web Worker to unfreeze the browser window.
        if (window.Worker) {
            runHighlightWorker();
        } else {
            highlightCode();
        }
    }


    return {
        run: run
    };
});



